.title-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.big-title {
  margin: 0;
  padding: 10px;
  font-size: 45px;
  font-weight: bold;
}

.sub-title {
  font-size: 16px;
}

.score-header {
  display: flex;
  gap: 40px;
  margin-top: 25px;
  justify-content: center;
  font-size: 30px;
  font-weight: bold;
}

.game-board {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  width: 650px;
}

.flag-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.country-code {
  font-weight: bold;
}

.flag-image {
  width: 200px;
  height: 140px;
  margin: 0;
  padding: 0;
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  transition: transform 0.5s;
}

.flag-image:hover {
  transform: scale(1.1);
  cursor: pointer;
}
